/* ============================================================================
   TIC TAC TOE BASIC (TUTORIAL-STYLE) - src/components/TicTacToeBasic/styles.css
   ============================================================================ */

/* === GAME SQUARE STYLING === */
.square {
  background: #fff; /* White background for clean tutorial look */
  border: 1px solid #999; /* Gray border around each square */
  float: left; /* Floats squares left to create grid layout */
  font-size: 24px; /* Large text for X and O visibility */
  font-weight: bold; /* Bold text for better contrast */
  line-height: 34px; /* Centers text vertically (matches height) */
  height: 34px; /* Square height in pixels */
  margin-right: -1px; /* Overlaps borders to avoid double-thickness */
  margin-top: -1px; /* Overlaps borders to avoid double-thickness */
  padding: 0; /* No internal spacing */
  text-align: center; /* Centers X/O horizontally */
  width: 34px; /* Square width in pixels (matches height) */
}

/* === WINNING SQUARE HIGHLIGHT === */
.winning-square {
  background-color: yellow; /* Bright yellow to highlight winning combination */
  font-weight: bold; /* Extra bold text for emphasis */
}

/* === BOARD ROW LAYOUT === */
.board-row:after {
  clear: both; /* Clears floated elements to start new row */
  content: ""; /* Required for ::after pseudo-element */
  display: table; /* Creates block-level element for clearing */
}

/* === GAME STATUS AND INFO === */
.status {
  margin-bottom: 10px; /* Adds space below status text */
}

.game-info {
  margin-left: 20px; /* Adds space to the left of game information */
}

/* ============================================================================
   TIC TAC TOE IMPROVED (CUSTOM STYLE) - src/components/TicTacToeImproved/styles.css
   ============================================================================ */

/* === BOARD LAYOUT === */
.board {
  display: grid; /* Uses CSS Grid for precise 3x3 layout */
  grid-template-columns: repeat(3, 1fr); /* Creates 3 equal-width columns */
  gap: 0px; /* No space between grid items */
}

.board-container {
  display: flex; /* Uses flexbox for centering */
  justify-content: center; /* Centers board horizontally */
  align-items: center; /* Centers board vertically */
  position: relative; /* Allows absolute positioning of child elements */
  width: 300px; /* Fixed width to accommodate game elements */
  height: 200px; /* Fixed height for consistent layout */
  overflow: visible; /* Allows elements to extend beyond container */
  margin-right: -30px; /* Adjusts positioning relative to other elements */
}

/* === SQUARE STYLING === */
.mysquare {
  background: rgb(35 39 47); /* Dark gray background matching site theme */
  border: 1px solid rgb(88 196 220); /* Light blue border matching site accent */
  color: rgb(88 196 220); /* Light blue text color */
  float: left; /* Floats for grid positioning */
  font-size: 35px; /* Large text for clear visibility */
  font-family: Optimistic Display, -apple-system, ui-sans-serif, system-ui,
    sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol,
    Noto Color Emoji;
  /* Same font as site for consistency */
  line-height: 34px; /* Vertical text centering */
  height: 50px; /* Larger squares than basic version */
  margin-right: -1px; /* Overlapping borders */
  margin-top: -1px; /* Overlapping borders */
  padding: 0; /* No internal spacing */
  text-align: center; /* Horizontal text centering */
  width: 50px; /* Square width matches height */
}

/* === HOVER EFFECTS === */
.mysquare.empty:hover {
  border: 2px solid rgb(88 196 220); /* Thicker border on hover for feedback */
  background-color: #19181a; /* Darker background on hover */
}

/* === WINNING SQUARE HIGHLIGHT === */
.my-winning-square {
  font-weight: bold; /* Bold text for winning squares */
}

/* === BUTTON STYLING === */
.mybutton,
.desc-button {
  background-color: rgb(35 39 47); /* Dark background matching theme */
  color: rgb(88 196 220); /* Light blue text */
  font-size: 16px; /* Standard button text size */
  margin: 0px 0px; /* No margin (reset default) */
  border: 1px solid rgb(88, 196, 220); /* Light blue border */
}

.mybutton:hover {
  border: 1px solid rgb(88, 196, 220); /* Maintains border color on hover */
  background-color: #19181a; /* Darker background on hover */
  color: rgb(88, 196, 220); /* Maintains text color */
}

.desc-button {
  border-radius: 0px; /* Square corners (not rounded) */
  margin-left: 145px; /* Positions button to the right */
}

.desc-button:hover {
  border: 1px solid rgb(88, 196, 220); /* Same hover effect as mybutton */
  background-color: #19181a; /* Darker background on hover */
  color: rgb(88, 196, 220); /* Maintains text color */
}

/* === GAME STATUS AND LAYOUT === */
.my-status {
  margin-bottom: -25px; /* Reduces space below status (tight layout) */
  text-align: center; /* Centers status text */
  margin-right: -30px; /* Adjusts positioning */
}

.my-game-info {
  width: 350px; /* Fixed width for consistent layout */
  min-height: 300px; /* Minimum height prevents layout shifts */
}

.move-info {
  margin-top: -10px; /* Reduces space above move information */
}

/* === WINNING LINE OVERLAY === */
.winning-overlay {
  position: absolute; /* Positions relative to board-container */
  top: 0; /* Aligns to top of container */
  left: 0; /* Aligns to left of container */
  width: 100%; /* Full width of container */
  height: 100%; /* Full height of container */
  overflow: visible; /* Allows drawing outside boundaries */
  pointer-events: none; /* Doesn't interfere with clicks */
}
