/* ============================================================================
   CONNECT FOUR GAME - src/components/ConnectFour/styles.css
   ============================================================================ */

/* === GAME BOARD === */
.c4board {
  display: flex; /* Uses flexbox for column layout */
  flex-direction: column; /* Stacks rows vertically */
  grid-template-rows: repeat(6, 50px); /* Defines 6 rows of 50px each */
  gap: 5px; /* 5px space between rows */
  background-color: rgb(
    88 196 220
  ); /* Light blue background (classic Connect 4 color) */
  padding: 5px 5px 10px 5px; /* Padding: 5px top/sides, 10px bottom */
  border-radius: 10px; /* Rounded corners for modern look */
  width: fit-content; /* Width adjusts to content size */
  position: relative; /* Allows absolute positioning of children */
}

/* === ROW LAYOUT === */
.row {
  display: flex; /* Uses flexbox for horizontal cell layout */
}

/* === GAME CELLS === */
.cell {
  width: 50px; /* Cell width */
  height: 50px; /* Cell height (makes perfect circles) */
  background-color: rgb(35 39 47); /* Dark gray for empty cells */
  border-radius: 50%; /* Makes cells perfectly circular */
  display: flex; /* Uses flexbox for centering content */
  justify-content: center; /* Centers content horizontally */
  align-items: center; /* Centers content vertically */
  margin: 3px; /* Space around each cell */
}

/* === PIECE COLORS === */
.cell.red {
  background-color: red; /* Red player pieces */
}

.cell.yellow {
  background-color: yellow; /* Yellow player pieces */
}

.cell.hovered {
  background-color: #19181a; /* Darker color when hovering over column */
}

/* === GAME STATUS === */
.c4-status {
  display: flex; /* Uses flexbox for layout */
  margin-bottom: 0px; /* No margin below status */
  justify-content: center; /* Centers status text horizontally */
}

/* === BUTTON STYLING === */
.c4-button {
  background-color: rgb(35 39 47); /* Dark background matching theme */
  color: rgb(88 196 220); /* Light blue text */
  font-size: 16px; /* Standard text size */
  margin: 0px 0px; /* No margin */
  border: 1px solid rgb(88, 196, 220); /* Light blue border */
  border-radius: 0px; /* Square corners */
  margin-left: 290px; /* Positions button to the right */
  margin-top: 22px; /* Adds vertical spacing */
}

.c4-button:hover {
  border: 1px solid rgb(88, 196, 220); /* Maintains border on hover */
  background-color: #19181a; /* Darker background on hover */
  color: rgb(88, 196, 220); /* Maintains text color */
}

/* === PLAYER INDICATOR PIECES === */
.piece {
  display: inline-block; /* Allows width/height on inline element */
  width: 20px; /* Small circle width */
  height: 20px; /* Small circle height */
  border-radius: 50%; /* Makes it circular */
  margin-left: 5px; /* Space from text */
  margin-top: 3px; /* Vertical alignment adjustment */
}

/* === PIECE COLOR CLASSES === */
.red {
  background-color: red; /* Red pieces */
}

.yellow {
  background-color: yellow; /* Yellow pieces */
}

/* === PLAYER INDICATOR LAYOUT === */
.player-indicator {
  display: flex; /* Uses flexbox for layout */
  align-items: center; /* Vertically centers text and piece */
  gap: 5px; /* Space between text and colored circle */
}
