/* ============================================================================
   GLOBAL.CSS - Site-wide styles applied to all components
   ============================================================================ */

/* Universal box-sizing rule (commented out - not currently active)
* {
  box-sizing: border-box; // Makes padding/border included in element width/height
}
*/

/* === BODY AND TYPOGRAPHY === */
body {
  background-color: #19181a; /* Dark background color from React tutorial */
  font-family: Optimistic Display, -apple-system, ui-sans-serif, system-ui,
    sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol,
    Noto Color Emoji;
  /* Font stack: Custom font first, then system fallbacks for cross-platform compatibility */
  font-weight: 400; /* Normal font weight (not bold, not thin) */
  opacity: 0.9; /* Slightly transparent for subtle effect */
}

/* === HEADING STYLES === */
h1,
h2,
h3,
h4 {
  font-family: Optimistic Display, -apple-system, ui-sans-serif, system-ui,
    sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol,
    Noto Color Emoji;
  /* Same font stack as body for consistency */
  color: rgb(88 196 220); /* Light blue color for all headings */
}

/* === HORIZONTAL RULE STYLING === */
hr {
  border-color: rgb(
    88 196 220
  ); /* Makes horizontal lines match heading color */
}

/* === LINK STYLES === */
:link {
  text-decoration: none; /* Removes underline from unvisited links */
  color: #4d5aa0; /* Purple-blue color for unvisited links */
}

:visited {
  text-decoration: none; /* Removes underline from visited links */
  color: #644451; /* Darker purple for visited links */
}

a:hover {
  text-decoration: underline; /* Adds underline when mouse hovers over link */
  color: blue; /* Changes to blue on hover for better UX */
}

/* === CODE FORMATTING === */
code {
  font-size: 1.2em; /* Makes code text 20% larger than normal text */
}

/* === LIST STYLING === */
ul {
  padding-inline-start: 18px; /* Indents unordered lists from the left */
}

ul li {
  margin-top: -12px; /* Reduces space above list items */
  margin-bottom: 18px; /* Adds space below list items for readability */
}

/* === MAIN CONTENT CONTAINER === */
.bigdiv {
  background-color: rgb(
    35 39 47
  ); /* Dark gray background for main content area */
  color: rgb(88 196 220); /* Light blue text color (matches headings) */
  width: 800px; /* Fixed width container */
  margin: auto; /* Centers the container horizontally */
  padding: 18px; /* Adds 18px of space inside the container */
  opacity: 1; /* Fully opaque (no transparency) */
  overflow: hidden; /* Hides any content that exceeds container boundaries */
  /* border:2px solid #BADA55 - commented out debugging border */
}

/* === GAME LAYOUT === */
.jsapp {
  display: flex; /* Uses flexbox layout */
  justify-content: center; /* Centers content horizontally */
  width: auto; /* Width adjusts automatically to content */
}

.game {
  display: flex; /* Uses flexbox layout */
  flex-direction: row; /* Arranges child elements in a horizontal row */
}

/* === CUSTOM CHECKLIST STYLING === */
.checklist {
  list-style: none; /* Removes default bullet points */
  counter-reset: item; /* Initializes a counter named 'item' to 0 */
  padding: 0; /* Removes default list padding */
}

.checklist li {
  position: relative; /* Allows absolute positioning of child elements */
  counter-increment: item; /* Increases the 'item' counter by 1 for each list item */
  margin: 0px; /* No margin around list items */
  padding-left: 25px; /* Adds space on left for custom numbering */
}

/* Adds custom numbering before each list item */
.checklist li::before {
  content: counter(item) ". "; /* Displays the counter value followed by a period */
  position: absolute; /* Positions element relative to parent li */
  left: 0; /* Aligns to the left edge */
  top: 0; /* Aligns to the top edge */
  transform: translate(
    60%,
    -15%
  ); /* Fine-tunes position (move right 60%, up 15%) */
}

/* Adds checkmark to completed items */
.checklist li.checked::after {
  content: "✔️"; /* Displays a checkmark emoji */
  position: absolute; /* Positions relative to parent li */
  left: 0; /* Aligns to left edge */
  top: 0; /* Aligns to top edge */
  color: #00ff00 !important; /* Forces green color (overrides other styles) */
  font-size: 1.2em; /* Makes checkmark 20% larger than normal text */
  transform: translate(5%, -40%); /* Fine-tunes position (right 5%, up 40%) */
}
